import React, { useState } from "react";
import style from "../../Academics/Academics.module.css";
import { useTranslation } from "react-i18next";
// import { useNavigate } from "react-router-dom";
import JoinFormInput from "../../../../../../JoiningForm/Student/Form/JoinFormInput";
import { useSubjectAttendaceUpdateTimeSlot } from "../../../../../../hooks/member_tab/subject-api";
import QLoader from "../../../../../../Loader/QLoader";
import { Notfication } from "../../../../../../validation/Snackbar";

const AttendaceTimeSlot = ({
  today,
  setState,
  state,
  sid,
  cid,
  // onClosePopup,
  flow,
}) => {
  const { t } = useTranslation();
  // const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
  });
  const [subjectAttendaceUpdateTimeSlot] = useSubjectAttendaceUpdateTimeSlot();

  // const onClose = () => {
  //   navigate(-1);
  // };
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };
  const onUpdate = () => {
    if (state?.from && state?.to && flow && cid) {
      setDisabled((pre) => !pre);
      const dates = today.split("-");
      const day = dates[0] > 9 ? dates[0] : `0${dates[0]}`;
      const month = dates[1] > 9 ? dates[1] : `0${dates[1]}`;
      subjectAttendaceUpdateTimeSlot({
        sid: sid,
        timeSlot: {
          date: `${day}/${month}/${dates[2]}`,
          from: timeFormatTable(state?.from),
          to: timeFormatTable(state?.to),
          cid: cid,
          flow: flow,
        },
      })
        .then((res) => {
          setDisabled((pre) => !pre);
          if (res.data?.already_slot_mark) {
            setNotificationState({
              msg: `For this time slot attendace has been already marked by ${
                res.data?.subject?.subjectName
              } - ${`${
                res.data?.subject?.subjectTeacherName?.staffFirstName ?? ""
              } ${
                res.data?.subject?.subjectTeacherName?.staffMiddleName ?? ""
              } ${
                res.data?.subject?.subjectTeacherName?.staffLastName ?? ""
              }`}`,
              run: true,
            });
          } else {
            setState((prev) => ({
              ...prev,
              is_updated: true,
              // is_hide: true,
            }));
            // onClosePopup();
          }
        })
        .catch({});
    }
  };

  // console.info("state", state);
  return (
    <>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "flex-start",
          gap: "2rem",
          padding: "1rem",
        }}
      >
        <JoinFormInput
          labelText={t("from")}
          placeholder="00:00"
          name={"from"}
          value={get24Format(state?.from)}
          type={"time"}
          onChange={onChange}
          customStyleContainer={{
            display: "flex",
            justifyContent: "flex-start",
            alignItems: "center",
            gap: "1rem",
          }}
        />
        <JoinFormInput
          labelText={t("to")}
          placeholder="00:00"
          name={"to"}
          value={get24Format(state?.to)}
          type={"time"}
          onChange={onChange}
          customStyleContainer={{
            display: "flex",
            justifyContent: "flex-start",
            alignItems: "center",
            gap: "1rem",
          }}
        />

        {state.is_mark ? null : (
          <div
            className={style.time_slot_btn}
            onClick={onUpdate}
            style={{
              textAlign: "center",
            }}
          >
            {t("update")}
          </div>
        )}

        {disabled && <QLoader />}
      </div>
      {notificationState.run && (
        <Notfication
          msg={notificationState.msg}
          run={notificationState.run}
          setRun={() => setNotificationState({ msg: "", run: false })}
          postiton="bottom"
          type="error"
        />
      )}
    </>
  );
};

export default AttendaceTimeSlot;

const timeFormatTable = (vale) => {
  const splitValue = vale?.split(":");
  let meridian = "";
  if (splitValue[0] > 12) {
    meridian = "Pm";
    splitValue[0] -= 12;
  } else if (splitValue[0] < 12) {
    meridian = "Am";
    if (splitValue[0] === 0) {
      splitValue[0] = 12;
    }
  } else {
    meridian = "Pm";
  }
  const hour = splitValue[0] > 9 ? splitValue[0] : `0${+splitValue[0]}`;
  let res = `${hour}:${splitValue[1]} ${meridian}`;
  return res?.substring(0, 8);
};

const get24Format = (data) => {
  let splt = data?.split(" ");
  let nt = splt?.[0]?.split(":");
  let hr = "";

  if (splt?.[1] === "Pm") {
    if (+nt?.[0] === 12) {
      hr = nt?.[0];
    } else {
      hr = +nt?.[0] + 12;
    }
  } else {
    hr = nt?.[0];
  }
  return `${hr}:${nt?.[1]}`;
};
